import React, { Component } from 'react'
//iconfont.cn 官网搜选图标，加入购物车
//点击页面右上角购物车，将图标添加进项目（没有项目就先点击“下载到本地”）
//解压压缩包，全部复制到项目public文件夹下，在index.html中通过link引入iconfont.css
//使用时，<i className = 'iconfont icon-具体名字'></i>
import { Carousel, WingBlank } from 'antd-mobile';
import Mytab from './Mytab'
import './home.css'

class Home extends Component {
    state = {
        data: ['1', '2', '3'],
        imgHeight: 176,
    }
    componentDidMount() {
        // simulate img loading
        setTimeout(() => {
            this.setState({
                data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
            });
        }, 100);
    }
    render() {
        return (
            <div className="big">
                <input type="text" placeholder="寻找宝贝店铺" onClick={() => this.props.history.push('/search')} style={{marginLeft:"18px",width:"335px"}}/>
                <WingBlank>
                    <Carousel
                        autoplay
                        infinite
                        beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                        afterChange={index => console.log('slide to', index)}
                    >
                        {this.state.data.map(val => (
                            <a
                                key={val}
                                href="http://www.alipay.com"
                                style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                            >
                                <img
                                    src={`https://zos.alipayobjects.com/rmsportal/${val}.png`}
                                    alt=""
                                    style={{ width: '100%', verticalAlign: 'top' }}
                                    onLoad={() => {
                                        // fire window resize event to change height
                                        window.dispatchEvent(new Event('resize'));
                                        this.setState({ imgHeight: 'auto' });
                                    }}
                                />
                            </a>
                        ))}
                    </Carousel>
                </WingBlank>
                <div className="home">
                    <img className="home1" src="image/book.jpg" onClick={()=>this.props.history.push('/book')}/>
                    <img className="home1" src="image/book1.jpg" onClick={()=>this.props.history.push('/book1')}/>
                    <img className="home1" src="image/book2.jpg" onClick={()=>this.props.history.push('/book2')}/>
                    <img className="home1" src="image/book3.jpg" onClick={()=>this.props.history.push('/book3')}/>
                    <img className="home1" src="image/book4.jpg" onClick={()=>this.props.history.push('/book4')}/>
                    <img className="home1" src="image/book5.jpg" onClick={()=>this.props.history.push('/book5')}/>
                    <img className="home1" src="image/book6.jpg" onClick={()=>this.props.history.push('/book6')}/>
                    <img className="home1" src="image/book7.jpg" onClick={()=>this.props.history.push('/book7')}/>
                    <img className="home1" src="image/book8.jpg" onClick={()=>this.props.history.push('/book8')}/>
                    <img className="home1" src="image/book9.jpg" onClick={()=>this.props.history.push('/book9')}/>
                </div>
                <Mytab />
            </div>
        );
    }
}
export default Home
