import React from 'react';
// import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
// import Page from './Page';
// import Home from './Home';
// import Text from './Text';
// import Teach from './Teach';
// import Blog from './Blog';
// import Comm from './Comm';
// import Login from './Login';
// import routes from '../router.config';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: this.props.location.pathname,
            hidden: false,
            fullScreen: false,
        };
    }

    renderContent(pageText) {
        return (
            <div style={{ backgroundColor: 'white', height: '100%', textAlign: 'center' }}>
                <div style={{ paddingTop: 60 }}>Clicked “{pageText}” tab， show “{pageText}” information</div>
                <a style={{ display: 'block', marginTop: 40, marginBottom: 20, color: '#108ee9' }}
                    onClick={(e) => {
                        e.preventDefault();
                        this.setState({
                            hidden: !this.state.hidden,
                            //为真隐藏，为假不隐藏
                        });
                    }}
                >
                    Click to show/hide tab-bar
          </a>
                <a style={{ display: 'block', marginBottom: 600, color: '#108ee9' }}
                    onClick={(e) => {
                        e.preventDefault();
                        this.setState({
                            fullScreen: !this.state.fullScreen,
                        });
                    }}
                >
                    Click to switch fullscreen
          </a>
            </div>
        );
    }

    render() {
        const {pathname} = this.props.location;
        return (
            <div style={{ position: 'fixed',  width: '100%', bottom: 0 }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#33A3F4"
                    barTintColor="white"
                    hidden={this.state.hidden}
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={<div style={{
                            width: '22px',
                            height: '22px',
                            background: 'url(https://zos.alipayobjects.com/rmsportal/sifuoDUQdAFKAVcFGROC.svg) center center /  21px 21px no-repeat'
                        }}
                        />
                        }
                        selectedIcon={<div style={{
                            width: '22px',
                            height: '22px',
                            background: 'url(https://zos.alipayobjects.com/rmsportal/iSrlOTqrKddqbOmlvUfq.svg) center center /  21px 21px no-repeat'
                        }}
                        />
                        }
                        // selected={this.props.match.path === '/home'}
                        selected={pathname === '/home'}
                        //确定状态
                        badge={1}
                        onPress={() => {
                            this.props.history.push('/home')
                        }}
                        data-seed="logId"
                    >
                        {/* {this.renderContent('Life')} */}
              
            </TabBar.Item>
                    <TabBar.Item
                        icon={
                            //在这里更改图片 <i styly={{}} className="iconfont icon-具体名字"></i>
                            <i style={{color:'red',fontSize:25}} className="iconfont icon-my"></i>
                        }
                        selectedIcon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(https://gw.alipayobjects.com/zos/rmsportal/ekLecvKBnRazVLXbWOnE.svg) center center /  21px 21px no-repeat'
                            }}
                            />
                        }
                        title="Koubei"
                        key="Koubei"
                        badge={'new'}
                        selected={pathname === 'text'}
                        onPress={() => {
                            this.props.history.push('/text')
                        }}
                    // data-seed="logId1"
                    >
                        {/* //类组件，所以要加this,函数组件的时候可以不加this */}
                        
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(https://zos.alipayobjects.com/rmsportal/psUFoAMjkCcjqtUCNPxB.svg) center center /  21px 21px no-repeat'
                            }}
                            />
                        }
                        selectedIcon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(https://zos.alipayobjects.com/rmsportal/IIRLrXXrFAhXVdhMWgUI.svg) center center /  21px 21px no-repeat'
                            }}
                            />
                        }
                        title="Friend"
                        key="Friend"
                        dot
                        selected={pathname === '/blog'}
                        onPress={() => {
                            this.props.history.push('/blog')
                        }}
                    >
                        
            </TabBar.Item>
                    <TabBar.Item
                        icon={{ uri: 'https://zos.alipayobjects.com/rmsportal/asJMfBrNqpMMlVpeInPQ.svg' }}
                        selectedIcon={{ uri: 'https://zos.alipayobjects.com/rmsportal/gjpzzcrPMkhfEqgbYvmN.svg' }}
                        title="My"
                        key="my"
                        selected={pathname === '/comm'}
                        onPress={() => {
                            this.props.history.push('/comm')
                        }}
                    >
                        
            </TabBar.Item>
                    <TabBar.Item
                        icon={<i className="iconfont icon-more"></i>}
                        selectedIcon={<i className="iconfont icon-more"></i>}
                        title="Smile"
                        key="my"
                        selected={pathname === '/teach'}
                        onPress={() => {
                            this.props.history.push('/teach')
                        }}>
                        
            </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}

export default withRouter (Mytab)