import React, { useEffect } from 'react'
import { HashRouter as Router, NavLink, Switch, Redirect } from 'react-router-dom'
import './Doc.css';
import {RouteWithSubRoutes} from './App'
import {withRouter} from 'react-router-dom'

const DocContent = (props) => {
    console.log(props);
    return <h1>
        {props.match.parmas}
    </h1>
}
const Doc = ({routes} ) => {
    console.log(routes[0].component.name);
    useEffect((props) => {
        if (!localStorage.getItem('userinfo')) {
            props.history.push({
                pathname: '/Login',
                state: props.location//记录state:props.location的路径，方便之后通过replace返回
            })
        }
    }, [])
    return (
        <div>
            <Router>
                <ol style={{ float: 'right' }}>
                    <li><NavLink to='/doc/core'>核心概念指引</NavLink></li>
                    <li><NavLink to='/doc/guide'>高级指引</NavLink></li>
                    <li><NavLink to='/doc/api'>API</NavLink></li>
                    <li><NavLink to='/doc/hooks'>Hooks</NavLink></li>
                </ol>
                <Switch>
                    {
                        routes.map((route, i) => (
                            <RouteWithSubRoutes key={i} component={DocContent} {...route} />
                        ))
                    }
                </Switch>
            </Router>
        </div>

    )
}

export default withRouter(Doc);