import React from 'react';

import { HashRouter as Route, NavLink} from 'react-router-dom'

const CommText = (props) =>{
    return <h1>
        {props.match.params.type}
    </h1>
}
// getData = (id) =>{
//     useEffect(()=>{
//         props.history.push({
//             pathname:'/comm/:id'
//         })
//     },[])
// }
const Comm = (props) => {
    console.log("Comm:",props);
    return (
        <div>
            <div>
                <Route path="/Comm/:type" component={CommText}/>
                {/* <Redirect from="/Comm" to="/Comm/tech"/>    */}
            </div>
            <ol style={{float:'right'}}>
                <li><NavLink to="/Comm/teach">技术支持</NavLink></li>
                <li><NavLink to="/Comm/team">团队介绍</NavLink></li>
                <li><NavLink to="/Comm/cour">课程</NavLink></li>    
            </ol>     
        </div>
    )
}

export default Comm