import React from 'react';
import { HashRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Header from './Header';
import routes from '../router.config';

export const PrivateRoute=({ component: Com, ...rest }) =>{
    return (
        <Route
            {...rest} 
            render={({ location }) =>
                localStorage.getItem('userinfo')
                    ? <Com {...rest} />
                    : (
                        <Redirect
                            to={{
                                pathname: "/login",
                                state: location
                            }}
                        />
                    )
            }
        />
    );
}

export const RouteWithSubRoutes = (props) => {
    console.log("内容::123",props.routes);

    if (props.pri) {
        return <PrivateRoute {...props}/>
    } else {
        return <Route 
            render={()=>{
              return <props.component {...props}/>}
            }
        />
    }
}
const App = () => {
    return <Router>
        <Header />
        <Switch>
            {
                routes.map((route, i) => {
                    return <RouteWithSubRoutes key={i} {...route } />
                })
            }
        </Switch>
    </Router>
}

export default App;